import cmdNavBar from "@/components/cmd-nav-bar/cmd-nav-bar.vue"
import cmdPageBody from "@/components/cmd-page-body/cmd-page-body.vue"
import cmdTransition from "@/components/cmd-transition/cmd-transition.vue"
import cmdInput from "@/components/cmd-input/cmd-input.vue"

export default {
	components: {
		cmdNavBar,
		cmdPageBody,
		cmdTransition,
		cmdInput
	},
	onLoad(options) {
		this.st_id = uni.getStorageSync('STID');
		let type = options.type;
		console.log(type)
		if(type=='nickname'){
			this.field == 'nickname',
			this.fieldinfo = '请输入您的昵称'
			this.nickname = true;

		}else if (type =='iphone'){
			this.field = 'iphone',
			this.fieldinfo = '请输入您的手机号'
			this.iphone=true;
		}else if(type =='address'){
			this.field = 'address',
			this.fieldinfo = '请输入您的地址'
			this.address = true
		}else{
			this.field = 'code',
			this.fieldinfo = '请输入您的身份证号'
			this.code = true
		}
	},
	data() {
		return {
			st_id:'',
		   field : '',
		   fieldinfo : '',
		   nickname:false,
		   name:false,
		   iphone:false,
		   address:false,
		   code:false
		};
	},
	methods: {
			formSubmit: function(e) {
				
				console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
				var formdata = e.detail.value
				uni.request({
					url: this.apiServer+'/staff/staff/save',
					method: 'POST',
					data: formdata,
					success: res => {
						if(res.data.err==0){
						uni.reLaunch({
							url: '/pages/index/info'
						})
						}else{
							uni.showModal({
								content: '数据修改失败',
								showCancel: false
							});
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
		/**
		 * 提交按钮点击执行
		 */
		fnModify() {
			//console.log(JSON.stringify(this.mobile));
		},
		/**
		 * 获取验证码
		 */

	}

}
